.home 
    height: 744px
    position: relative
    background: url('../images/home-bg.jpg') no-repeat center 
    background-size: cover
    color: $color-text-base
    padding-top: 11.7%
    margin-bottom: 96px

.home__content 
    width: 100%
    max-width: 420px

.home__title 
    font-size: px-to-vw(72, 1200)
    line-height: 1
    letter-spacing: 1.08px
    font-weight: 400
    margin-bottom: 26px

.home__text 
    font-size: 20px
    line-height: 24px
    font-weight: 400
    margin-bottom: 44px

.home__btn 
    width: 125px

.home__more-btn
    position: absolute
    bottom: 24px
    left: 50%
    transform: translateX(-50%)