.review-card
    width: 100%
    background-color: $color-text-base
    border-radius: 8px
    padding: 9%
    color: #000

.review-card__pic 
    max-width: 223px
    max-height: 223px
    margin: 0 auto
    margin-bottom: 30px

.review-card__text
    font-size: 20px
    line-height: 1.25
    letter-spacing: 0.3px
    margin-bottom: 16px
    overflow: hidden
    display: -webkit-box
    -webkit-line-clamp: 4
    -webkit-box-orient: vertical
    @include max-w(769)
        font-size: 14px

.review-card__author
    font-size: 14px
    line-height: 20px
    color: #767676