.header
  padding: 17px 23px 19px 20px

.header__wrap 
  align-items: center

.header__menu
  position: relative
  width: 100%
  align-items: center

.header__logo
  display: block
  min-width: 97px
  height: 30px
  margin-right: 29px

.header__logo-img

.header__nav
  align-items: center
  
  @include max-w(769)
    max-width: 300px
    height: 100%
    position: fixed
    right: 0
    top: 0
    flex-direction: column
    padding: 20px
    padding-top: 65px
    background: $color-background
    z-index: 2
    transform: translate(100%)
    // transition: transform 0.5s ease-in-out

.header__menu input[type="checkbox"] 
  display: none
  position: absolute
  height: 23px
  width: 35px
  top: 50%
  transform: translateY(-50%)
  right: 0
  z-index: 5
  opacity: 0
  @include max-w(769)
    display: block

.hamburger-lines 
  display: none
  height: 23px
  width: 35px
  position: absolute
  right: 0
  top: 50%
  transform: translateY(-50%)
  z-index: 3
  flex-direction: column
  justify-content: space-between
  @include max-w(769)
    display: flex

.hamburger-lines .line 
  display: block
  height: 4px
  width: 100%
  border-radius: 10px
  background: $color-text-base

.header__menu .hamburger-lines .line1 
  transform-origin: 0% 0%
  transition: transform 0.4s ease-in-out

.header__menu .hamburger-lines .line2 
  transition: transform 0.2s ease-in-out

.header__menu .hamburger-lines .line3 
  transform-origin: 0% 100%
  transition: transform 0.4s ease-in-out

.header__menu-list 
  margin: 0 -10px
  @include max-w(769)
    flex-direction: column

.header__menu input[type="checkbox"]:checked,
.header__menu input[type="checkbox"]:checked ~ .hamburger-lines
  position: fixed
  right: 25px
  top: 32px

.header__menu input[type="checkbox"]:checked ~ .header__nav
  overflow-y: scroll
  transform: translateX(0)
  transition: transform 0.5s ease-in-out

.header__menu input[type="checkbox"]:checked ~ .hamburger-lines .line1 
  transform: rotate(35deg)

.header__menu input[type="checkbox"]:checked ~ .hamburger-lines .line2 
  transform: scaleY(0)

.header__menu input[type="checkbox"]:checked ~ .hamburger-lines .line3 
  transform: rotate(-35deg)

.header__menu-item 
  padding: 0 10px
  @include max-w(769)
    margin-bottom: 20px

.header__link
  font-size: px-to-vw(16, 1200)
  line-height: 20px
  color: #A6A8AD
  @include max-w(769)
    font-size: 16px
  
.header__actions
  align-items: center

.header__login
  margin-right: 20px

.header__signup  
  font-size: 16px
  line-height: 18px 
  color: $color-background
  background: #FFFFFF
  border: 1px solid #FFFFFF
  border-radius: 32px 
  padding: 8px 21px