.product-card 
    width: 100%
    align-items: center
    color: $color-text-base
    @include max-w(769)
        flex-wrap: wrap
        justify-content: center
    
.product-card__text 
    width: 100%
    max-width: 440px
    @include max-w(769)
        text-align: center

.product-card__type
    font-size: 12px
    line-height: 14px
    letter-spacing: 0.12px
    text-transform: uppercase
    color: #A6A8AD
    margin-bottom: 5px

.product-card__title
    font-size: 28px
    font-weight: 400
    line-height: 36px
    margin-bottom: 12px

.product-card__descr
    font-size: 16px
    line-height: 24px
    margin-bottom: 23px

.product-card__btn
.product-card__pic
    width: 100%
    max-width: 580px
    max-height: 386px
    @include max-w(769)
        order: 2

.product-card__img