.footer
  padding: 48px 0 60px
  color: #E5E2DE

.footer__wrap 

.footer__logo 
  width: 72px
  height: 53px

.footer__logo-img
.footer__content
  width: 100%
  max-width: 800px

.footer__menu 
  margin-bottom: 80px

.grid-item 
  float: left
  width: calc( 100% / 6 )
  @include max-w(769)
    width: calc( 100% / 3 )
  @include max-w(480)
    width: calc( 100% / 2 )

.grid-item:not(:last-child)
  @include min-w(860)
    height: 105px

.grid:after 
  content: ''
  display: block
  clear: both

.footer__menu-name
  font-size: 20px
  line-height: 24px
  margin-bottom: 12px

.footer__menu-list 
  max-width: 153px

.footer__menu-item:not(:last-child)
  margin-bottom: 11px

.footer__link
  font-size: 12px
  line-height: 18px
  color: #A6A8AD

.footer__bottom
  flex-wrap: wrap
  
.footer__copyright  
  font-size: 12px
  line-height: 18px
  color: #A6A8AD
  margin-right: 48px

.footer__bottom-list
  flex-wrap: wrap
  margin-right: 35px

.footer__bottom-item:not(:last-child)
  margin-right: 30px

.footer__socials 
.footer__social-icon
  display: block
  width: 16px
  height: 16px

.footer__social-img
  vertical-align: bottom
