*,
*::before,
*::after{
  box-sizing: border-box
}

html {
  scroll-behavior: smooth;
}
  
img {
  width: 100%;
  height: auto;
}

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p{
  margin: 0;
  padding: 0;
}

body{
  background-color: $color-background;
  font-family: $font-family-base;
}

.container{
  max-width: 1135px;
  padding: 0 15px;
  margin: 0 auto;
}

.flex{
  display: flex;
}

.flex-between{
  justify-content: space-between;
}

.flex-center{
  justify-content: center;
}

.text-right{
  text-align: right;
}
    




